
.input-custom {
    font-weight: 600;
}

.uppercase {
  text-transform: uppercase;
}

.day-custom {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 600;
}

.input-custom:focus {
  border-color: #d3d3d3; /* Светло-серый цвет */
  box-shadow: 0 0 0 0.2rem rgba(211, 211, 211, 0.5);
}


.input-custom::placeholder {
  color: #b5b5b5; /* Светло-серый цвет */
  opacity: 1; /* Убедитесь, что он полностью видим */
}

.multi-select-list {
  display: flex;
  flex-direction: column;
  border: 1px solid #ccc;
  border-radius: 5px;
  max-width: 300px;
  padding: 10px;
  background: #f9f9f9;
  cursor: pointer;
}

.list-item {
  padding: 8px;
  margin-bottom: 5px;
  border: 1px solid transparent;
  transition: background 0.3s;
}

.list-item:hover {
  background: #e0e0e0;
}

.list-item.selected {
  background: #007bff;
  color: white;
  border-color: #0056b3;
}